import React, { Component } from 'react';
import { auth } from '../firebase';
import { Button, TextField } from '@mui/material'; //Adding MUI components styling

class Login extends Component {
    state = { email: '', password: '', error: null };

    handleInputChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    };

    handleSubmit = async (event) => {
        event.preventDefault();
        const { email, password } = this.state;

        try {
            await auth.signInWithEmailAndPassword(email, password);
        } catch (error) {
            this.setState({ error: error.message });
        }
    };

    render() {
        const { email, password, error } = this.state;

        return (
            <div>
                <form onSubmit={this.handleSubmit}>
                    <TextField //replacing input with TextField from MUI
                        name="email"
                        type="email"
                        placeholder="Email"
                        value={email}
                        onChange={this.handleInputchange}
                    />
                    <TextField //replacing input with TextField
                     name="password"
                     type="password"
                     placeholder="Password"
                     value={password}
                     onChange={this.handleInputChange}
                    />
                    {error ? <p>{error}</p> : null}
                    <Button type="submit">Login</Button> //replacing button with Button from MUI
                </form>
            </div>
        );
    }
}

export default Login;

